@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@600&display=swap");

@font-face {
  font-family: "MAG-Font";
  src: url("~@/assets/fonts/MAG-Font.woff2") format("woff2"),
    url("~@/assets/fonts/MAG-Font.woff") format("woff"),
    url("~@/assets/fonts/MAG-Font.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "MAG-Font";
  src: url("~@/assets/fonts/MAG-Font_1.woff2") format("woff2"),
    url("~@/assets/fonts/MAG-Font_1.woff") format("woff"),
    url("~@/assets/fonts/MAG-Font_1.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

// Game
#BUG {
  position: relative;
  height: 100%;
  width: 100%;
  transition: background-color 2.5s;
  font-family: "Roboto Mono", monospace;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.Page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  transition: background-color 2.5s;
}
#Dashboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Logo {
  display: block;
  margin: 0 auto;
  margin-bottom: 40px;
  width: 100%;
  max-width: 340px;
}
#PhaserGame {
  cursor: crosshair;
}

// HUD
.PostGame_Overlay {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
}
.PostGame_Message {
  font-family: "MAG-Font";
  font-size: 7.5em;
  color: white;
}
.HUD_Bottom {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  .HUD_Bottom_Left {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-grow: 1;
    text-align: left;
  }
  .HUD_Bottom_Right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-grow: 1;
    text-align: right;
  }

  .HUD_Life {
    padding: 0 10px;
  }
  .HUD_LifeBar {
    height: 5px;
    .HUD_LifeBar_Fill{
      transition: all 0.1s;
      height: 100%;
    }
    &[data-level="high"] {
      .HUD_LifeBar_Fill{
        background: rgba(255, 255, 255, 0.45);
      }
    }
    &[data-level="med"] {
      .HUD_LifeBar_Fill{
        background: #FCE00A;
      }
    }
    &[data-level="low"] {
      .HUD_LifeBar_Fill{
        background: #EF3A65;
      }
    }
  }
  .HUD_Lives {
    width: 25px;
    height: 20px;
    background: url("~@/assets/hud-life.svg");
    display: inline-block;
    margin: 0 5px;
  }
}
.TotalScore {
  font-family: "MAG-Font";
  font-size: 100px;
  color: #fce00a;
}

.Controls {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
}
.Button {
  cursor: pointer;
  width: 100%;
  font-family: "Roboto Mono";
  font-size: 20px;
  font-weight: 600;
  box-shadow: -5px 6px 0 2px #00b7ff;
  background: blue;
  color: white;
  padding: 12.5px 20px;
  margin-bottom: 15px;
  transition: transform 0.1s;

  &[data-color="magenta"] {
    background-color: #8b0975;
  }
  &[data-color="purple"] {
    background-color: #380762;
  }

  &:hover {
    transform: scale(1.05);
  }
}

[data-background="violet"] {
  background-color: #202a51;
}
[data-background="navy"] {
  background-color: #1e2235;
}
[data-background="jazz"] {
  background-color: #16235b;
}
[data-background="purple"] {
  background-color: #341c47;
}
[data-background="black"] {
  background-color: #000;
}
[data-background="fx"] {
  background-color: rgba(0, 0, 0, 0.692);
}
[data-background="steps"] {
  transition: background-color 0.1s;
  &[data-audio-step="1"] {
    background: #160C1E;
  }
  &[data-audio-step="2"] {
    background: #060F2D;
  }
  &[data-audio-step="3"] {
    background: #031723;
  }
  &[data-audio-step="4"] {
    background: #060321;
  }
  &[data-audio-step="5"] {
    background: #001151;
  }
  &[data-audio-step="6"] {
    background: #1F0438;
  }
}

.Audio_Background {
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  z-index: -1;

  flex-wrap: wrap;
  [data-level] {
    opacity: 0;
    flex-grow: 1;
    min-width: 50px;
  }
  [data-level="0"],
  [data-level="1"],
  [data-level="2"] {
    background: #ff7887;
  }
  [data-level="2"],
  [data-level="3"],
  [data-level="4"],
  [data-level="5"],
  [data-level="6"],
  [data-level="7"] {
    background: #ffa371;
  }
  [data-level="8"],
  [data-level="9"],
  [data-level="10"],
  [data-level="11"],
  [data-level="12"],
  [data-level="13"] {
    background: #fff06d;
  }
  [data-level="9"],
  [data-level="10"],
  [data-level="11"],
  [data-level="12"],
  [data-level="13"],
  [data-level="14"] {
    background: #d8ff83;
  }
  [data-level="15"],
  [data-level="16"],
  [data-level="17"],
  [data-level="18"],
  [data-level="19"],
  [data-level="20"] {
    background: #73c0ff;
  }
  [data-level="21"],
  [data-level="22"],
  [data-level="23"],
  [data-level="24"],
  [data-level="25"],
  [data-level="26"] {
    background: #bc88ff;
  }
  [data-level="27"],
  [data-level="28"],
  [data-level="29"],
  [data-level="30"],
  [data-level="31"],
  [data-level="32"] {
    background: #ff8be6;
  }
}

[data-background="burgundy"] {
  background-color: #42061a;
  transition: background-color 0.5s;
  .Button {
    &[data-color="magenta"] {
      background-color: #cc3154;
    }
    &[data-color="purple"] {
      background-color: #380762;
    }
    box-shadow: -5px 6px 0 2px #fce00a;
  }
}

[data-text-color="yellow"] {
  color: #fce00a;
}

.shake {
	-webkit-animation: shake 0.05s linear infinite both;
	        animation: shake 0.05s linear infinite both;
}
@keyframes shake {
  0% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
  20% {
    -webkit-transform: translate(-5px, 5px);
            transform: translate(-5px, 5px);
  }
  40% {
    -webkit-transform: translate(-5px, 5px);
            transform: translate(-5px, 5px);
  }
  60% {
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
  }
  80% {
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
  }
  100% {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

