}
.LeaderBoard_Player {

}
.LeaderBoard_Score {

}
#Name_Input {
  color: white;
  margin-bottom: 10px;
  .Name_Input_Instructions {
    color: white;
    font-size: 22px;
    margin-bottom: 10px;
    text-align: left;
